body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow: hidden;
    background-color: #f4f4f5;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  
  .sidebar {
    background-color: #1d1d23;
    height: 100vh;
    padding: 6px;
  }
  
  .sidebarMenuItem {
    color: #94949f;
  }
  
  .panel {
    background-color: #2d2d36;
    height: 100vh;
    padding: 16px;
    border-radius: 8px;
  }
  
  .search {
    width: 100%;
    border-radius: 8px;
    border: 1px solid #d1d1d1;
    padding: 8px 16px;
    font-size: 0.875rem;
    font-weight: 500;
    outline: none;
  }
  
  .sidebarMenuItem:hover {
    color: #2153cc;
    cursor: pointer;
  }
  
  .zoom {
    position: absolute;
    left: 60%;
    background-color: #fff;
    bottom: 20px;
    border-radius: 8px;
  }

  .pointer {
      cursor: pointer;
  }

  .tab {
    margin-bottom: 10px;
    text-align: center;
    display: flex;
    justify-content: space-around;
    .tab-item {
      padding: 0 16px 6px 16px;
      cursor: pointer;
      color: rgb(148, 148, 159);
      font-weight: 500;
      &.active {
        cursor: default;
        color: #eee;
        border-bottom: 4px solid rgb(33, 83, 204);
      }
    }
  }
.text-wrapper {
  margin-top: 4px;
  .text-group {
    margin-top: 1rem;
    .panel-item {
      cursor: pointer;
      padding: 8px;
      margin-bottom: 8px;
      &:hover {
        background-color: #343441;
        border-radius: 8px;
        transition: all 0.3s ease;
      }
    }
  }
}